import React, { Component } from 'react'
import copy from 'copy-to-clipboard';
import Link from 'gatsby-link'
import { Button, Steps, Card, Checkbox, Row, Col, Table, message, Tag, Modal } from 'antd';
const Step = Steps.Step;
import "./index.css";

const STEP_ELEMENTS = [
  {
    name: 'HTML'
  }, {
    name: 'CSS'
  }, {
    name: 'Fonts'
  }, {
    name: 'Images'
  }, {
    name: 'JavaScript'
  }
];

const DATA_SOURCE = {
  HTML: [
    {
      name:'html',
      what:'HTML',
      importance: 'medium',
      why:'HTML',
      config: "const HtmlWebpackPlugin = require('html-webpack-plugin');"
    },
    {
      name:'',
      what: '',
      importance: 'low',
      why:'',
      config: "const HtmlWebpackPlugin = require('html-webpack-plugin');"
    },
    {
      name: '',
      what: '',
      importance: 'low',
      why: 'text/csstext/javascript, ',
      example: <div>
        <p></p>
        <pre>
          <p style={{marginBottom:0}}>{'<script type="text/javascript"></script>'}</p>
        </pre>
        <p></p>
        <pre>
          <p style={{marginBottom:0}}>{'<script></script>'}</p>
        </pre>
      </div> 
    },
    {
      name: 'CSSJavaScript',
      what: 'CSSJavaScript',
      importance: 'high',
      why: 'CSSJavaScript.',
      example: <div>
        <p></p>
        <pre>
          <p style={{marginBottom:0, color:'red'}}>{'<script src="jquery.js"></script>'}</p>
          <p style={{marginBottom:0, color:'red'}}>{'<script src="foo.js"></script>'}</p>
          <p style={{marginBottom:0}}>{'<link rel="stylesheet" href="foo.css"/>'}</p>
        </pre>
        <p></p>
        <pre>
          <p style={{marginBottom:0}}>{'<link rel="stylesheet" href="foo.css"/>'}</p>
          <p style={{marginBottom:0, color:'green'}}>{'<script src="jquery.js"></script>'}</p>
          <p style={{marginBottom:0, color:'green'}}>{'<script src="foo.js"></script>'}</p>
        </pre>
      </div>
    },
    {
      name: 'iframe',
      what: 'iframe',
      importance: 'high'
    }
  ],
  CSS: [],
  Fonts: [],
  Images: [],
  JavaScript: []
};

const ROW_SELECTION = {
  onChange: (key, row) => {},
  getCheckboxProps: record => ({
    disabled: record.name === '',
    name: record.name
  })
};

function copyConfig (record) {
  copy(record.config);
  message.success('!');
}

class IndexPage extends Component {

  columns = [
    { dataIndex: 'name', title: '' },
    { 
      dataIndex: 'importance',
      title: '',
      render: (text, record) => {
        if (record.importance === 'high') return <Tag color="red"></Tag>;
        if (record.importance === 'medium') return <Tag color="yellow"></Tag>;
        if (record.importance === 'low') return <Tag color="green"></Tag>;
      }
    },
    { dataIndex: 'what', title: '' },
    { dataIndex: 'why', title: '' },
    { 
      key: 'operation', 
      title: '',
      render: (text,record) => {
        if (record.config) return <Button onClick={() => {copyConfig(record)}}>webpack</Button>;
        if (record.example) return <Button onClick={() => this.showExampleModal(record)}></Button>
        else return '';
      }
    }
  ];

  constructor(props){
    super(props)
    this.state = {
      currentStep: 0,
      todoList: [],
      modalVisible: false
    }
  }

  showExampleModal = (record) => {
    this.setState({
      modalVisible: true,
      modalContent: record.example
    });
  }

  _renderStepArea = () => {
    const { currentStep } = this.state;
    return (
      <Steps current={currentStep}>
        {
          STEP_ELEMENTS.map(step => {
            return <Step key={step.name} title={step.name} />;
          })
        }
      </Steps>
    );
  }

  _renderOperations = () => {
    const { currentStep } = this.state;
    return (
      <div className="operation-wrapper">
        <div className="opeartion-left">
          { 
            currentStep > 0
            &&
            <Button onClick={()=>{this.setState({currentStep: currentStep-1})}} className="operation-button"></Button>
          }
          {
           currentStep < STEP_ELEMENTS.length - 1
           && 
           <Button onClick={()=>{this.setState({currentStep: currentStep+1})}} className="operation-button" type="primary"></Button>
          }
        </div>
        <div className="opeartion-right">
          <Button className="operation-button" type="ghost"></Button>
          <Button className="operation-button" type="primary">webpack</Button>
        </div>
      </div>
    )
  }

  _renderCheckList = () => {
    const { currentStep } = this.state;
    const currentSection = STEP_ELEMENTS[currentStep].name;
    const currentDataSource = DATA_SOURCE[currentSection];
    return (
      <div style={{marginTop:10}}>
        <Table 
        pagination={false}
        className="checklist-table"
        rowSelection={ROW_SELECTION}
        rowKey="name" 
        bordered 
        columns={this.columns} 
        dataSource={currentDataSource} 
        />
      </div>
    );
  }

  _renderExampleModal = () => {
    const { modalVisible, modalContent } = this.state;
    return (
      <Modal
        title=""
        visible={modalVisible}
        footer={null}
        onCancel={() => { this.setState({modalVisible:false}); }}
      >
        {modalContent}
      </Modal>
    );
  }

  render () {
    return (
      <div className="container">
        <h3></h3>
        {this._renderStepArea()}
        {this._renderOperations()}
        {this._renderCheckList()}
        {this._renderExampleModal()}
      </div>
    );
  }
}

export default IndexPage



